import React from 'react';
import {
  ProfileTwoTone,
  ContactsTwoTone,
  SmileTwoTone,
  TrophyTwoTone,
  TagsTwoTone,
  RocketTwoTone,
  ProjectTwoTone,
  ToolTwoTone,
  ScheduleTwoTone,
} from '@ant-design/icons';
import _ from 'lodash-es';
import { ResumeConfig } from '@/components/types';

/**
 *  
 *  
 */
export const MODULES = ({
  i18n,
  titleNameMap,
}: {
  i18n: any;
  titleNameMap?: ResumeConfig['titleNameMap'];
}) => {
  return [
    { name: i18n.get(''), icon: <ContactsTwoTone />, key: 'avatar' },
    { name: i18n.get(''), icon: <ProfileTwoTone />, key: 'profile' },
    {
      name: i18n.get(''),
      icon: <ScheduleTwoTone />,
      key: 'educationList',
    },
    { name: i18n.get(''), icon: <SmileTwoTone />, key: 'aboutme' },
    { name: i18n.get(''), icon: <TrophyTwoTone />, key: 'awardList' },
    { name: i18n.get(''), icon: <ToolTwoTone />, key: 'workList' },
    { name: i18n.get(''), icon: <RocketTwoTone />, key: 'skillList' },
    { name: i18n.get(''), icon: <TagsTwoTone />, key: 'workExpList' },
    {
      name: i18n.get(''),
      icon: <ProjectTwoTone />,
      key: 'projectList',
    },
  ].map(d => {
    const name = _.get(titleNameMap, d.key);
    return { ...d, name: _.isNil(name) ? d.name : name };
  });
};

